.cursor {
  height: 62px;
  width: 62px;
  border-radius: 150px;
  border: 1px solid $color2;
  position: absolute;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: 300ms ease-out;
  z-index: 2;
  backdrop-filter: brightness(115%);

  &.hovered {
    width: 30px;
    height: 30px;
  }
}
