@font-face {
  font-family: "font1";
  src: url(../fonts/RobotoMono-Regular.ttf);
}
@font-face {
  font-family: "font2";
  src: url(../fonts/RobotoSlab-Regular.ttf);
}

$font1: "font1", sans-serif;
$font2: "font2", sans-serif;

$color1: white;
$color2: #73877b;
$color3: #2b2118;
$color4: #f1fffa;
$color5: #9191e9;
$black: black;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
  color: $color1;
}

body {
  overflow: hidden;
  background: linear-gradient(to bottom right, $color2, $color3);
  height: 100vh;
  font-family: $font2;
}
