.home {
  height: 100vh;
  width: 100vw;
  position: absolute;

  &:after {
    content: "";
    height: 800px;
    width: 800px;
    background: linear-gradient(to top left, $color2, $color3);
    border-radius: 50%;
    position: absolute;
    bottom: -480px;
    right: 0px;
    z-index: -2;
    animation: homeAnim 26s infinite alternate;

    @media screen and (max-width: 650px) {
      height: 400px;
      width: 400px;
      bottom: -270px;
    }

    @keyframes homeAnim {
      50% {
        transform: scale(0.8);
      }
      100% {
        transform: scale(1.1);
      }
    }
    @media screen and (max-width: 430px) {
      display: none;
    }
  }

  .home-main {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 95%;

    @media screen and (max-width: 530px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    img {
      width: 250px;
      height: 250px;
      border-radius: 300px;

      @media screen and (max-width: 900px) {
        width: 180px;
        height: 180px;
      }

      @media screen and (max-width: 530px) {
        width: 130px;
        height: 130px;
      }
    }

    .main-content {
      user-select: none;
      width: 40%;
      margin: 20px;

      h1 {
        font-size: 3rem;
        position: relative;
        font-family: $font1;
        padding-bottom: 20px;
        color: $color3;

        @media screen and (max-width: 850px) {
          font-size: 1.5rem;
        }
        @media screen and (max-width: 530px) {
          font-size: 1.2rem;
        }

        &:before {
          content: "";
          position: absolute;
          height: 350px;
          width: 350px;
          border-radius: 50%;
          border: 1px $color3 solid;
          top: -40px;
          left: -180px;
          z-index: -1;
          animation: h1Anim 1.5s forwards ease;
          transform: scale(0);

          @keyframes h1Anim {
            to {
              transform: scale(1);
            }
          }
        }
      }
      p {
        font-size: 1.2rem;
        font-weight: 100;

        @media screen and (max-width: 850px) {
          font-size: 1rem;
        }

        @media screen and (max-width: 530px) {
          font-size: 0.8rem;
        }
      }
    }
  }
}
